@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  height: 100vh;
  display: flex;
  grid-gap: 20px;
  gap: 20px;
}

.custom-scroll::-webkit-scrollbar {
  width: 8px;
}
.custom-scroll::-webkit-scrollbar-track {
  background-color: transparent;
}

.custom-scroll::-webkit-scrollbar-thumb {
  border-radius: 20px;
  background-color: rgb(180, 180, 180);
  -webkit-transition: 1000ms;
  transition: 1000ms;
}
.custom-scroll::-webkit-scrollbar-thumb:hover {
  background-color: gray;
}

.note-container {
  height: 100%;
  margin-left: 3vw;
  padding: 15px;
  width: 100%;
}
.note-container h2 {
  font-size: 2.7rem;
  font-family: "Poppins";
  margin-bottom: 14px;
  margin-left: 1rem;
}
.note-container-notes {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 20px;
  gap: 20px;
  height: 90%;
  overflow-y: scroll;
}
.note-container-note {
  margin: 10px;
}

.notes {
  padding: 25px;
  height: 280px;
  width: 260px;
  display: flex;
  flex-direction: column;
  border-radius: 30px;
}
.notes_text {
  flex: 1 1;
  resize: none;
  background-color: transparent;
  border: 0px solid;
  font-size: 1rem;
  line-height: 1.275rem;
  font-family: "Poppins";
  font-weight: 500;
}
.notes_text:focus {
  outline: none;
}

.notes_date {
  margin-top: 5px;
  color: rgb(77, 77, 77);
}

.note_footer {
  display: flex;
  justify-content: space-between;
}
.note_footer img {
  height: 30px;
  width: 30px;
  cursor: pointer;
  opacity: 0;
  transition: 300ms;
}
.notes:hover .note_footer img {
  opacity: 1;
}
.bold {
  font-size: 1.2rem;
  color: black;
}

.searchbar {
  margin-top: 5px;
  position: relative;
  margin-left: 20px;
}
.searchbar i {
  position: absolute;
  color: rgb(187, 187, 187);
  top: 10px;
  left: 10px;
  font-size: 16px;
}
.searchbar_input {
  padding: 10px;
  color: rgb(187, 187, 187);

  border-radius: 50px;
  width: 100%;
  padding-left: 35px;
  border: 0px solid;
  font-size: 16px;
}
.searchbar_input:focus {
  outline: none;
}

.sidebar {
  display: flex;
  flex-direction: column;
  grid-gap: 40px;
  gap: 40px;
  border-right: 1px solid rgba(121, 121, 121, 0.199);
  padding-right: 20px;
  padding: 15px;
}

.sidebar h2 {
  font-size: 1.2rem;
  margin-top: 10px;
}
.sidebar_img img {
  height: 50px;
  width: 50px;
  cursor: pointer;
  margin-top: 4rem;
  align-items: center;
  margin-left: 8px;
}
.sidebar_img {
  align-items: center;
}
.sidebar_list {
  align-items: center;
  display: flex;
  grid-gap: 15px;
  gap: 15px;
  flex-direction: column;
  height: 0;
  transition: 300ms ease;
}

.sidebar_list_active {
  height: 300px;
}
.sidebar_listitem {
  border-radius: 50%;
  height: 21px;
  width: 21px;
  list-style-type: none;
  cursor: pointer;
}

